@use "base/variables" as v;
@use "base/mixins" as m;

.register {
    h3 {
        color: v.$primaryBlue;
        text-align: center;
        margin-top: 3rem;
    }
    p {
        padding-left: 2rem;
        padding-right: 2rem;
    }
    .contact-form {
        border: 1px solid v.$primaryBlue;
        margin: 2rem;
        @include m.tablet {
            width: 80%;
            margin: 8rem auto;
        }
        @include m.desktop {
            width: 50%;
            margin: 8rem auto 12rem;
        }
    }
    form {
        padding: inherit 1rem;
        label {
            padding-top: 1rem;
        }
        .form-check {
            margin: 1rem auto 2rem;
        }
        .form-btn {
            width: 100%;
            button {
                width: 100%;
                @include m.tablet {
                    width: unset;
                }
            }
        }
    }
    .form-options {
        margin-top: 2rem;
        margin-bottom: 2rem;
        margin-left: 1rem;
        a {
            display: block;
            color: v.$secondaryGray;
            text-align: left;
            margin: 0;
            padding: 0;
            &:hover {
                color: v.$primaryBlueDark;
            }
        }
    }
}